* { 
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color:black;
}

header {
  background-image: url(./pastel_background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}

h1 {
  font-family: 'Rubik', sans-serif;
  font-size: 100px;
  margin: 0 auto;
  padding-top: 125px;
  text-align: center; 
}

.input-text-bar {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

form {
  text-align: center;
}

fieldset {
  border: none;
}

label {
  font-family: 'Rubik', sans-serif;
  font-size: 30px;
  background: white;
  padding: 15px;
  box-shadow: 5px 5px grey;
}

input[type="text"] {
  padding: 10px 315px 10px 0px;
  margin-top: 50px;
  border: none;
  box-shadow: 5px 5px grey;
}

.arrow {
  text-align: center;
  font-size: 50px;
  margin-top: 25px;
}

.submit-button {
  display: flex;
  justify-content: center;
  margin: 25px 0 25px 0;

}

button {
  font-family: 'Rubik', sans-serif;
  font-size: 30px;
  background-color: white;
  border: none;
  box-shadow: 5px 5px grey;
  padding: 15px;
}

.list-of-books {
  height: 100vh;
}

p {
  font-family: 'Rubik', sans-serif;
  font-size: 30px;
}

.fa-square {
  margin-right: 20px;
}